@import url(https://fonts.googleapis.com/css?family=Concert+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Monoton&display=swap);
@import url(https://fonts.googleapis.com/css?family=Concert+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    height: 100%;
    z-index: -1;


}


.TileContainer{
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto auto auto;
    width: 66vh;
    height: 66vh;
    position: relative;
    top: 16vh;
    z-index: 0;
    align-items: center;
    justify-content: center;
    margin: auto;   
  }

.TileContainer[allMatched='true']{
  -webkit-filter: blur(3px);
          filter: blur(3px);
  opacity: 0.75;   
}

.levelSelector[levelSelected='true'] {
  display: none;
}

.levelSelector[levelSelected='false'] {
  display: inline;
}

.youWon[allMatched='false']{
  display: none;
}

.youWon{ 
  position: relative;
  top: -30vh;
  margin: auto;
  z-index: 100;
  width: 50vh;
  color:  #9381FF;
  font-weight: bolder;
  font-family: 'Concert One';
  text-align: center;
}

.youWon h1 {
  font-size: 4.5vh;
  padding: 1vh;
}

.youWon h2 {
  font-size: 3vh;
  padding: 1vh;
}

.TileFront {
    display: flex;
    flex-direction: column;
    background-color: #FFEEDD;
    padding: 1.5vh;
    margin: 0.5vh;
    width: 12vh;
    height: 12vh;
    border-radius: 2vh;
    border-width: 0.5px;
    border-color: whitesmoke;
    border-style: solid;
    box-shadow: 0.5vh 0.5vh 1vh lightgrey;
    align-items: center;
    justify-items: center;
    -ms-user-select: none;
        user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
  }

.TileFront[matched='true']{
  opacity: 0.40;
}

.TileFront:hover {
    transition: 0.3s ease;
    transform: scale3d(1.15, 1.15, 1.15);
}

.TileFront img {
    width: 12vh;
    align-content: center;
    justify-content: center;
    display: flex;
 }
  
.TileBack {
    display: flex;
    flex-direction: column;
    background-color: #FFEEDD;
    background: repeating-radial-gradient(
                circle,
                #FFEEDD,
                #FFEEDD 4px,
                #FFD8BE 4px,
                #FFD8BE 10px
    );
    padding: 1.5vh;
    margin: 0.5vh;
    width: 12vh;
    height: 12vh;
    border-radius: 2vh;
    border-width: 0.5px;
    border-color: whitesmoke;
    border-style: solid;
    box-shadow: 0.5vh 0.5vh 1vh lightgrey;
    align-items: center;
    justify-items: center;
    -ms-user-select: none;
        user-select: none;
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -o-user-select: none;
        
    
  }

.TileBack:hover {
    transition: all 0.3s ease;
    opacity: .75;
    transform: scale3d(1.15, 1.15, 1.15);
}


.TileBack .image-container img {
    width: 5rem;
    align-content: center;
    justify-content: center;
    display: none;
 }
  
.Header {
    display: flex;
    position: fixed;
    background-color: #B8B8FF;
    justify-content: center;
    align-content: center;
    box-shadow: 0.5vh 0.5vh 1vh lightgrey;
    width: 100%;
    height: 14vh;
    top: 0;
    z-index: 2;
    margin: auto;
   
}

.Header h1 {
    font-family: 'Monoton', cursive;
    font-size: 12vh;
    color: #F8F7FF;
    text-shadow: 0.5vh 0.5vh 1vh #9381FF;
    margin: auto;
}
.Footer {
    display: flex;
    flex-direction: row;
    position: fixed;
    bottom: 0;
    z-index: 2;
    height: 16vh;
    width: 100%;
    background-color: #B8B8FF;
    justify-content: center;
    align-items: center;
    font-family: 'Concert One';
    font-weight: bolder;
    color: #F8F7FF;
    -ms-user-select: none;
        user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}

.Scores {
    background-color: #9381FF;
    padding: 0vh;
    margin-right: 1.5vh;
    width: 30vh;
    height: 10vh;
    border-radius: 6vh;
    display: flex;
    flex-direction: row;
    align-self: center;
    align-items: center;
    justify-content: space-between;
}

.Scores h2 {
    font-size: 4vh;
    text-align: center;
    padding: 0;
    padding-left: 3.5vh;
    align-self: center;
    margin: 0;
    
}

.Scores h3 {
    font-size: 5.5vh;
    background-color: lightcoral;
    line-height: 10vh;
    border-radius: 6vh;
    width: 10vh;
    text-align: center;
}

.NewGameButton {
    background-color: #9381FF;
    padding: 0vh;
    margin-left: 1.5vh;
    width: 30vh;
    height: 10vh;
    border-radius: 6vh;
    display: flex;
    flex-direction: row;
    align-self: center;
    align-items: center;
    justify-content: space-around;
}

.NewGameButton h2 {
    font-size: 4.5vh;
}

.NewGameButton:hover {
    transition: all 0.3s ease;
    transform: scale3d(1.1, 1.1, 1.1);
}
